<template>
    <div id="app">
        <router-view />
    </div>
</template>

<script>
    export default {
        name: 'App',
    }
</script>

<style>
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");
    #loading{
        display: flex;
        justify-content: center;
        align-items: center;

        position: fixed;
        width: 100%;
        height: 100vh;
        background-color: rgba(59, 129, 255,0.2);

        transition: 0.5s;

        z-index: 100;
    }

    body{
        margin: 0;
    }
    form{
        display: flex;
        flex-flow: column;
        flex-wrap: wrap;
        align-items: center;

        width: 100%;
        height: 60vh;
    }
    #title {
        display: flex;
        justify-content: center;

        margin-top: 20vh;

        font-size: 100px;
        text-align: end;
        color: rgb(59, 129, 255);
    }
    .text_box{
        width: 20%;
        min-width: 250px;
        height: 20px;

        padding: 15px;

        border: 1px solid rgb(224, 224, 224);
        border-radius: 10px;
        font-size: 20px;
    }
    .def_btn{
        cursor: pointer;

        height: 50px;
        width: 250px;

        margin: 10px 10px;

        background-color: rgb(59, 129, 255);
        font-size: 20px;
        color: white;
        border: 0px solid grey;
        border-radius: 10px;

        opacity: 0.8;
        transition: 0.5s;

        box-shadow: 2px 2px 5px grey;
    }
    .def_btn:hover{
        opacity: 1;
    }
    a:link{
        text-decoration: none;
    }

    .slide-fade-enter,
    .slide-fade-leave-to
        {
        transform: translateY(10px);
        opacity: 0;
    }
</style>


