<template>
    <div class="main">
        <transition name="slide-fade">
            <div id="loading" v-if="loading">
                <Loader :size="'20vh'" :color="'rgb(59, 129, 255)'"></Loader>
            </div>
        </transition>
        <router-link to="/">
            <h1 id="title">I Music</h1>
        </router-link>
        <div class="line"></div>
        <form @submit="loginServer()">
            <input v-model="username" type="text" id="username" class="text_box" placeholder="USERNAME" minlength="4"
                maxlength="20" required>
            <input v-model="password" type="password" id="pw" class="text_box" placeholder="PASSWORD" minlength="4"
                maxlength="20" required>
            <router-link to="/register" class="lost_password">비밀번호를 잊어버렸습니까?</router-link>
            <button class="def_btn" type="submit">로그인</button>
            <router-link to="/register"><button class="def_btn">회원가입</button></router-link>
        </form>
    </div>
</template>

<script>
    import axios from 'axios';
    import Loader from 'vue-spinner/src/BounceLoader.vue'

    export default {
        name: 'login_p',
        created() {
        },
        data() {
            return {
                username: "",
                password: "",
                loading: false
            }
        },
        components: {
            Loader
        },
        methods: {
            loginServer() {
                var mainPath = "http://58.145.101.15:3000"
                this.loading = true;
                axios.post(mainPath + "/auth/login", {
                    username: this.username,
                    password: this.password
                }).then(data => {
                    this.loading = false;
                    this.$router.push("/")
                }).catch(err => {
                    this.loading = false;
                    alert("유저이름 또는 비밀번호가 일치하지 않습니다.");
                })
            }
        }
    }
</script>


<style scoped>
    #title {
        margin: 8% 0 20px 0;
    }

    .main {
        display: flex;
        flex-flow: column;
        flex-wrap: wrap;
        align-items: center;
    }

    .line {
        width: 600px;
        height: 2px;

        margin: 10px;

        background-color: rgb(224, 224, 224);
        border-radius: 50px;
    }

    .lost_password {
        width: 22%;
        color: rgb(200, 200, 200);

        margin-bottom: 3%;
    }

    .text_box {
        margin: 15px;
    }
</style>


