<template>
    <div class="main">
        <nav>
            <span id="user_name" @click="user_pop = !user_pop">{{ user_name }}</span>
            <i class="material-icons size_32" id="user_icon">perm_identity</i>
        </nav>
        <transition name="slide-fade">
            <section v-if="user_name == '로그인'" v-show="user_pop" id="user_pop">
                <router-link to="/login"><button class="btn" id="login">로그인</button></router-link>
                <router-link to="/register"><button class="btn" id="register">회원가입</button></router-link>
            </section>
            <section v-else v-show="user_pop" id="user_pop">
                <router-link to="/list"><button class="btn" id="login">내 곡 목록</button></router-link>
                <button @click="logout()" class="btn" id="register">로그아웃</button>
            </section>
        </transition>
        <router-link to="/"><h1 id="title">I Music</h1></router-link>
        <input v-model="search_data" type="text" id="search" placeholder="검색" @change="gotoSearch()">
    </div>
</template>

<script>
    import axios from 'axios';
    var mainPath = "http://58.145.101.15:3000"
    
    export default {
        name: 'main_p',
        created(){
                axios.post(mainPath + "/auth/token")
                .then(data => {
                    if(data.status == 200){
                        this.user_name = data.data.username
                    }
                    else{
                        this.user_name = "로그인"
                    }
                }).catch(err => {
                    this.user_name = "로그인"
                    console.log(err);
                })
        },
        data() {
            return {
                user_pop: 0,
                user_name: "로그인",
                search_data: ""
            }
        },
        methods :{
            gotoSearch(){
                this.$router.push("/search")
            },
            logout(){
                axios.post(mainPath + "/auth/logout")
                .then(data => {
                    this.user_name = "로그인"
                }).catch(err => {
                    this.user_name = "로그인"
                    console.log(err);
                })
            }
        }
    }
</script>


<style scoped>
    .test{
        opacity: 0;
    }

    nav {
        display: flex;
        justify-content: flex-end;
        align-items: center;

        width: 100%;
        height: 110px;
    }

    #user_name {
        cursor: pointer;

        font-size: 20px;
        color: rgb(59, 129, 255);
    }

    #user_icon {
        font-size: 40px;

        margin: 0 40px 0 20px;

        border-radius: 100%;
        background-color: rgb(200, 200, 200);
        color: white;

    }

    section {
        position: fixed;
        right: 40px;
        top: 100px;

        width: 270px;
        height: 140px;

        background-color: rgb(59, 129, 255);
        border-radius: 10px;

        box-shadow: 5px 5px 10px grey;

        transition: 0.5s;
    }

    .btn {
        height: 50px;
        width: 250px;

        margin: 10px 10px;

        background-color: white;
        font-size: 20px;
        color: rgb(59, 129, 255);
        border-radius: 10px;

        box-shadow: 2px 2px 5px grey;

        opacity: 0.8;

        transition: 0.5s;
    }
    .btn:hover{
        opacity: 1;
    }

    .main {
        display: flex;
        flex-flow: column;
        flex-wrap: wrap;
        align-items: center;
    }
    #search {
        width: 40%;
        height: 20px;

        padding: 15px;

        border: 1px solid rgb(224, 224, 224);
        border-radius: 10px;

        font-size: 20px;
    }
</style>


